
import { mapState, mapActions } from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

export default {
components: {
Tab,
TabItem,
Flexbox,
FlexboxItem,
Clocker,
XButton
},
data () {
return {

}
},
computed: {
...mapState([
'pintuanAllGroup'
])
},
methods: {
...mapActions([
'getPintuanAllGroup'
]),
clickJoin (group) {
this.$router.push({
name: 'PintuanProduct',
query: {
actEntityId: group.actEntityId,
autoJoin: true
}
})
},
clickInvite (group) {
sessionStorage.setItem('showShareBox', true)
this.$router.push({
name: 'PintuanProduct',
query: {
actEntityId: group.actEntityId
}
})
},
formatDate (time) {
return dateFormat(time, 'YYYY-MM-DD')
}
},
created () {
const z = this
z.getPintuanAllGroup({
status: 1
}).then(function (allGroups) {

})
},
mounted () {
console.log(this.pintuanAllGroup)
}
}



